<template>
  <div id="menu-component">
    <div id="nav-open" v-on:click="clickmenu">
      <span></span>
    </div>
    <transition name="slide-fade">
      <div class="menu" :class="{active : isActive}" v-show="isActive">
        <ul type="square">
          <span v-for="destination in menus" :key="destination.path">
            <router-link tag="li" :to="{path : destination.path}
            ">
              <li class="cursor" v-on:click="clickmenu">{{destination.name}}</li>
            </router-link>
          </span>
        </ul>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: "Menu",
  props: {
    bgClass: {
      type: String,
      default: ""
    }
  },
  data: () => ({
    isActive: false,
    menus: null
  }),
  mounted() {
    this.setMenu();
  },
  methods: {
    clickmenu() {
      this.isActive = !this.isActive;
    },
    setMenu() {
      this.menus = this.$router.options.routes;
    }
  }
};
</script>

